import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import day1 from '@/pages/day1'
import day2 from '@/pages/day2'
import day4 from '@/pages/day4'
import day5 from '@/pages/day5'
import day6 from '@/pages/day6'
import day7 from '@/pages/day7/father'
import yinru from '@/pages/yinru'
import day6exer2 from '@/pages/day6exer2'
import first from '@/pages/zhangning/8.3'
import day8 from '@/pages/day8/table'
import bayuesihao from '@/pages/zhangning/bayuesihao'





import dd3 from '@/pages/d3'
import jieloulianxi from '@/pages/jieloulianxi'
import VF from '@/pages/VF'
import fuyong from '@/pages/fuyong'
import route from '@/pages/day9/route'
import humanDetail from '@/pages/humanDetail'





Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      component: (r)=>require(['@/pages/login/index2.vue'],r)
    },
    {
      path: '/register',
      component: (r)=>require(['@/pages/login/index2.vue'],r)
    },
    {
      path: '/humanDetail',
      component: humanDetail
    },
    {
      path: '/route',
      component: route
    },
    {
      path: '/day8',
      component: day8
    },
    {
      path: '/day6exer2',
      component: day6exer2
    },
    {
      path: '/day4',
      component: day4
    },
    {
      path: '/day3',
      component: dd3
    },
    {
      path: '/',
      component: r=> require(['@/pages/home/homeIndex.vue'],r)
    },
    {
      path: '/bookContainer',
      component: r=> require(['@/pages/home/bookContainer.vue'],r)
    },


    {
      path: '/day1',
      component: day1
    },
    {
      path: '/day2',
      component: day2
    },
    {
      path: '/jieloulianxi',
      component: jieloulianxi
    },
    {
      path: '/day5',
      component: day5
    }, {
      path: '/day7',
      component: day7
    },
    {
      path: '/yinru',
      component: yinru
    },
    {
      path: '/first',
      component: first
    },
    {
      path: '/bayuesihao',
      component: bayuesihao
    },
  ]
})



