<template>
  <div>
    <el-table :data="tableData">
      <el-table-column label="序号">
        <template slot-scope="scope">
          {{ scope.$index + 1 }}
        </template>
      </el-table-column>
      <el-table-column label="姓名" prop="username"></el-table-column>
      <el-table-column label="查看详情">
        <template slot-scope="scope">
          <el-button @click="toDetail(scope.row.userid)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  created() {
    axios.get("/api/getHumans").then(r => {
      if (r.data.code == 200) {
        this.tableData = r.data.data;
      }
    });
  },
  methods: {
    toDetail(id) {
      this.$router.push("/humanDetail?id=" + id);
    }
  },
  data() {
    return {
      tableData: []
    };
  }
};
</script>



