<template>
  <div>
    <el-button @click="changeName()">{{buttonName}}</el-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      buttonName: "按钮名称",
    };
  },
  methods: {
    changeName() {
      this.buttonName = "点击了按钮";
    },
  },
};
</script>

<style  scoped>
</style>


