<template>
	<div class="body">
		<div class="panorama"><img class="p_img" src="images/panorama/new_york.jpg" alt="panorama" /></div>
		<div class="wrapper">
			<div class="header">
				<h1>
					<a class="no-animation">{{name}}</a>
				</h1>
				<h2> {{target_jobs.join(' / ')}} </h2>
				<div class="clear"></div>
			</div>
			<section class="content">
				<ul class="content-list">
					<li class="list-home">
						<!-- home start -->
						<div class="column">
							<div class="g_15 f_right">
								<img class="portrait" :src="logo" alt="portrait" />
							</div>
							<div class="g_15 f_left">
								<blockquote class="about">{{slogan}}<br />

								</blockquote>
								<p class="about_me">{{describe}}</p>
							</div>
						</div>
						<div class="column p_top_none">
							<div class="g_15 g_620_padding">
								<h3 class="heading">
									<span>+</span> 职业生涯
									<span>
										<i class="icon-briefcase"></i>
									</span>
								</h3>
								<ul class="employment" v-for="(j,ji) in job_history" :key="ji">
									<li class="time">{{j.year}}</li>
									<li class="company">
										<span>{{j.name}}</span>
									</li>
									<li class="work">{{j.job}}</li>

								</ul>

							</div>
							<div class="g_15 g_620_padding">
								<h3 class="heading">
									<span>+</span> 专业知识
									<span>
										<i class="icon-cloud"></i>
									</span>
								</h3>
								<table class="skills">
									<tr v-for="(s,si) in skills" :key="si">
										<td class="desc">{{s.name}}</td>
										<td>
											<div class="outer-bar">
												<!-- {{s.score}} -->
												<div class="inner-bar" :style="`width:${s.score}%`"></div>
											</div>
										</td>
									</tr>

								</table>
								<h3 class="heading">
									<span>+</span> 学历
									<span>
										<i class="icon-book"></i>
									</span>
								</h3>
								<ul class="education">
									<li class="time">{{education_year}}</li>
									<li class="institution">
										<span>{{education_school}}</span>
									</li>
									<li class="grade">{{education_degree}}</li>
								</ul>
							</div>
						</div>
					</li>
				</ul>
			</section>
		</div>
	</div>
</template>
<script>
require("./js/jquery.min.js");
require("./js/custom.js");
export default {
  created() {
    this.getHumanInfo();
    window.v = this;
  },
  data() {
    return {
      name: "",
      logo: "",
      target_jobs: [],
      slogan: "",
      describe: "",
      job_history: [],
      skills: [],
      education_school: "",
      education_year: "",
      education_degree: ""
    };
  },
  methods: {
    // 获取个人信息
    getHumanInfo() {
      let userid = this.$route.query.id;
      axios.get("/api/getHumanCV?userid=" + userid).then(r => {
        if (r.data.code == 200) {
          this.name = r.data.data.name;
          this.logo = r.data.data.logo;
          this.target_jobs = r.data.data.target_jobs;
          this.slogan = r.data.data.slogan;
          this.describe = r.data.data.describe;
          this.job_history = r.data.data.job_history;
          this.skills = r.data.data.skills;
          this.education_school = r.data.data.education.school;
          this.education_year = r.data.data.education.year;
          this.education_degree = r.data.data.education.degree;
        }
      });
    }
  }
};
</script>
<style scoped>
@import "./css/reset.min.css";
@import "./css/style.css";
@import "./css/reset.min.css";
@import "./ts/themeswitcher.css";

.skills .inner-bar {
  display: inherit;
}
</style>



