<template >
  <jieloulianxi  @success="ooo()" ppp="18">1111 bbbb</jieloulianxi>
</template>
<script>
import jieloulianxi from "./jieloulianxi";
export default {
  components: {
    jieloulianxi,
  },
  methods:{
    ooo(){
      this.$message(
        {message:"成功",type:"success"}
      )
    }
  }
};
</script>


