
<template>
    <div>
        <!-- <el-button v-for="(b,bi) in buttons" :key="bi">{{b.name}}</el-button> -->

        <!-- 不显示的按钮 -->
        <!-- <el-button v-if="false">啊啊啊啊啊</el-button> -->

        <!-- 不显示的按钮 -->
        <!-- <el-button v-if="1==2">别别别</el-button> -->

        <!-- 人数大于50的按钮 -->
        <!-- <el-button v-for="(b,bi) in buttons" :key="bi" v-if="b.crew_count>50">{{b.name}}</el-button> -->

        <!-- 不同颜色按钮写法1 -->
        <!-- <div>
            <el-button v-for="(b,bi) in buttons" :key="bi" v-if="b.crew_count>50" type="primary">{{b.name}}</el-button>
            <el-button v-for="(b,bi) in buttons" :key="bi" v-if="b.crew_count<=50" type="danger">{{b.name}}</el-button>
        </div> -->

        <!-- 不同颜色按钮写法2 -->
        <!-- <div>
            <div v-for="(b,bi) in buttons" :key="bi">
                <el-button v-if="b.crew_count>50" type="primary">{{b.name}}</el-button>
                <el-button v-if="b.crew_count<=50" type="danger">{{b.name}}</el-button>
            </div>
        </div> -->

        <!-- 不同颜色按钮写法3 -->
        <!-- <el-button v-for="(b,bi) in buttons" :key="bi" :type="b.crew_count>50?'primary':'danger'">{{b.name}}</el-button> -->

        <!-- 不同颜色按钮写法4 -->
        <el-button v-for="(b,bi) in buttons" :key="bi" :type="getType(b)">{{b.name}}</el-button>
    </div>
</template>

<script>
export default {
  methods: {
    getType(item) {
      if (item.crew_count > 50) {
        return "primary";
      } else {
        return "danger";
      }
    }
  },
  data() {
    return {
      buttons: [
        { name: "人事部", crew_count: 55 },
        { name: "采购部", crew_count: 30 },
        { name: "研发部", crew_count: 180 },
        { name: "设计部", crew_count: 8 },
        { name: "销售部", crew_count: 70 }
      ]
    };
  }
};
</script>

<style>
</style>




