<template>
  <div>
    <el-table :data="tabledata">
      <el-table-column label="id" prop="id"></el-table-column>
      <el-table-column label="姓名" prop="name"></el-table-column>
      <el-table-column label="点击">
          <template slot-scope="scope">
              <el-button @click="ppp(scope.row.id)">查看{{scope.row.name}}的简历</el-button>
          </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  created() {
    axios.get("http://localhost:3000/getHumans").then((e) => {
      this.tabledata = e.data.data;
    });
  },
  data() {
    return {
      tabledata: [{ id: "1", name: "1" }],
    };
  },
  methods: {
      ppp(t){
          window.open("http://localhost:8080/#/humanDetail?id="+t)
      }
  },
};
</script>


