<template>
  <div>

    {{v1}} {{v2}}
    <el-input v-model="value"></el-input>

    <el-button v-if="isCorrect" type="success" icon="el-icon-check" circle></el-button>
    <el-button v-if="!isCorrect" type="danger" icon="el-icon-close" circle></el-button>

  </div>
</template>

<script>
export default {
  watch: {
    value(newValue, oldValue) {
      if (newValue == this.v1 + this.v2) {
        console.log("答案正确");
        this.isCorrect=true
      } else {
        console.log("答案错误");
        this.isCorrect=false
      }
    }
  },
  data:function() {
    return {
      v1: 3,
      v2: 2,
      value: "",
      isCorrect: false
    };
  }
};
</script>

<style>
</style>



