<template>
  <div>
    <el-row>
      {{is_correct}}
      <el-tag>{{d1}}</el-tag>
      <el-tag type="danger">{{d2}}</el-tag>
      <el-input v-model="val"></el-input>
      <el-button @click="ppp()">点我</el-button>
    </el-row>
  </div>
</template>
<script>
export default {
  props: {
    ppp: "",
  },
  watch: {
    is_correct(v) {
      if (!this.val) {
        return;
      }
      if (v) {
        this.$confirm("回答正确,是否进行下一题", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
        }).then((e) => {
          if (e == "confirm") {
            this.d1 = this.random();
            this.d2 = this.random();
            this.val = "";
          }
        });
      } else {
        this.$message({
          message: "回答错误",
          type: "error",
        });
      }
    },
  },

  computed: {
    is_correct() {
      if (this.ppp == 1) {
        return this.val == this.d1 + this.d2;
      } else {
        return this.val == this.d1 * this.d2;
      }
    },
  },
  data() {
    return {
      d1: 9,
      d2: 8,
      val: 0,
    };
  },
  created() {
    window.v = this;
  },
  methods: {
    ppp() {
      this.$emit("success");
    },
    d13() {
      return parseInt(this.d1) + parseInt(this.val) * 2;
    },
    random() {
      return parseInt(Math.random() * 10);
    },
  },
};
</script>


