<template>
  <div>
    <h1>我是爸爸</h1>
    <el-tabs v-model="tab">
      <el-tab-pane name="孩子1" label="孩子1"></el-tab-pane>
      <el-tab-pane name="孩子2" label="孩子2"></el-tab-pane>
    </el-tabs>

    <son1 v-if="tab=='孩子1'"></son1>
    <son2 v-if="tab=='孩子2'"></son2>
  </div>
</template>

<script>
import son1 from "./son1.vue";
import son2 from "./son2.vue";

export default {
  components: {
    son1,
    son2,
  },

  data() {
    return {
      tab: "",
    };
  },
};
</script>


