<template>
  <div>
    <el-button v-for="i in 10" :key="i" @click="gotoDay(i)">第{{i}}天</el-button>

  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    gotoDay(day) {
      let path = `/day${day}`;
      this.$router.push(path);
    }
  }
};
</script>

<style scoped>
</style>



