<template>
  <div>
    {{d1}}{{d2}}
    <el-input v-model="val" placeholder></el-input>
    <span v-if="d1+d2==val && max_second>0">答案正确</span>
    <span v-if="max_second<=0">已超时</span>
    <span v-if="d1+d2!=val && max_second>0">剩余时间{{max_second}}秒</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      d1: 0,
      d2: 0,
      val: 0,
      max_second: 10,
    };
  },
  watch: {
    val(v) {
      if (v == this.d1 + this.d2 && this.max_second > 0) {
        this.$confirm("回答正确，是否回答下一题？", "提示").then((r) => {
          if (r == "confirm") {
            this.d1 = this.random();
            this.d2 = this.random();
            this.max_second = 10;
            this.val = "";
          }
        });
      }
    },
  },
  beforeDestroy() {
    clearInterval(window.it);
  },
  created() {
    this.d1 = this.random();
    this.d2 = this.random();

    let that = this;
    window.it = setInterval(function () {
      that.max_second--;
    }, 1000);
  },
  methods: {
    random() {
      return parseInt(Math.random() * 10);
    },
  },
};
</script>


