<template>
  <div>
    <el-tag>{{d1}}</el-tag>
    <el-tag type="success">+</el-tag>
    <el-tag>{{d2}}</el-tag>
=
    <el-input v-model="val">回答正确</el-input>
    <p v-if="d1+d2==val && max_second>0 ">回答正确</p>
    <p v-if="max_second<=0">已超时</p>
    <p v-if="d1+d2!=val && max_second>0">剩余时间{{max_second}}秒</p>
  </div>
</template>
<script>
export default {
  watch: {
    val(v) {
      if (v == this.d1 + this.d2) {
        this.pop();
      }
    },
  },
  beforeDestroy() {
    clearInterval(window.it);
  },
  data() {
    return {
      d1: 1,
      d2: 2,
      val: 0,
      max_second: 10,
      show: true,
      n: 0,
    };
  },
  created() {
    let that = this;
    window.it = setInterval(function () {
      that.max_second--;
    }, 1000);
    this.d1 = this.random();
    this.d2 = this.random();
  },
  methods: {
    random() {
      return parseInt(Math.random() * 10);
    },
    pop() {
      this.n++;
      this.$confirm(`您已回答正确${this.n}道题，是否进入下一题?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.d1 = this.random();
          this.d2 = this.random();
          this.val = "";
          this.max_second = 10;
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
};
</script>
<style  scoped>
.el-input {
  width: 150px;
}
</style>


